import React, { useState,useEffect } from "react";
const TulaNew2 = () => {
  const [text, settext] = useState(" ");

  function handleCopy() {
    const copyText = document.querySelector("#bot4");
    copyText.select();
    document.execCommand("copy");
    window.getSelection().removeAllRanges();
    copyText.style.color = "red";
  }
  useEffect(() => {
    handleSave();
   }, []);
  const handleTextarea = (e) => {
  };

  const handleSave = () => {
    let Vrushabha = localStorage.getItem("props.textareaValue10") || "";
    let TulaNew2 = localStorage.getItem("props.textareaValue12") || "";
    const indexVrushabha  = Vrushabha.indexOf("*");
    const indexTulaNew2 = TulaNew2.indexOf("*");
    console.log(" in Vrushabha ",indexVrushabha)
    console.log(" in TulaNew2 ",indexTulaNew2)
    let firsthalf = Vrushabha.slice(0,indexVrushabha).replace("*","");
    let secondhalf =  TulaNew2.slice(indexTulaNew2).replace("*","");
    let texty = (firsthalf+secondhalf).replace("","");
    settext(texty)
  };

  return (
    <div className="container">
       <h3>Latest rasi phalalu</h3>
      <div>
        <h4>1.TulaNew2 New</h4>
      </div>
      <textarea id = "bot4" rows = "30" cols = "auto"   value={text} onChange={handleTextarea} />
      <button className="mx-2 my-2" onClick={handleSave}>Update</button>
      <button className="mx-2 my-2" onClick={handleCopy}>Copy</button>
     
</div>
  );
};

export default TulaNew2
