import React, { useState ,useEffect} from "react";
const MeenaNew = () => {
  const [text, settext] = useState(" ");

  function handleCopy() {
    const copyText = document.querySelector("#bot10");
    copyText.select();
    document.execCommand("copy");
    window.getSelection().removeAllRanges();
    copyText.style.color = "red";
  }

  const handleTextarea = (e) => {
  };
  useEffect(() => {
    handleSave();
   }, []);
  const handleSave = () => {
    let midhuna = localStorage.getItem("props.textareaValue4") || "";
    let Meena = localStorage.getItem("props.textareaValue3") || "";
    const indexmidhuna  = midhuna.indexOf("*");
    const indexMeena = Meena.indexOf("*");
    console.log(" in midhuna ",indexmidhuna)
    console.log(" in Meena ",indexMeena)
    let firsthalf = midhuna.slice(0,indexmidhuna).replace("*","");
    let secondhalf =  Meena.slice(indexMeena).replace("*","");
    let texty = (firsthalf+secondhalf).replace("","");
    settext(texty)
  };

  return (
    <div className="container">
      <div>
        <h4>2.Meena New</h4>
      </div>
      <textarea id = "bot10" rows = "30" cols = "auto"   value={text} onChange={handleTextarea} />
      <button className="mx-2 my-2" onClick={handleSave}>Update</button>
      <button className="mx-2 my-2" onClick={handleCopy}>Copy</button>
     
</div>
  );
};

export default MeenaNew
