import React from 'react';
import { animateScroll as scroll, scroller } from 'react-scroll';

class ScrollButtons extends React.Component {

  scrollToBottom2 = () => {
    scroll.scrollMore(-200, {
      duration: 500,
      smooth: 'easeInOutQuint',
    });
  };
  scrollToBottom3 = () => {
    scroll.scrollMore(200, {
      duration: 500,
      smooth: 'easeInOutQuint',
    });
  };


  render() {
    return (
      <div className="scroll-buttons-container">
     
        <button style = {{"position": 'absolute' , "top" : "200px","right": "-10px"}}className="scroll-button" onClick={this.scrollToBottom2}>
        
        </button>
        <button style = {{"position": 'absolute' , "top" : "250px","right": "-10px"}} className="scroll-button" onClick={this.scrollToBottom3}>
        
        </button>
        <div id="scroll-bottom" />
      </div>
    );
  }
}

export default ScrollButtons;
