import React, { useState,useEffect } from "react";
const MithunaNew = () => {
  const [text, settext] = useState(" ");
  useEffect(() => {
    handleSave();
   }, []);
  function handleCopy() {
    const copyText = document.querySelector("#bot6");
    copyText.select();
    document.execCommand("copy");
    window.getSelection().removeAllRanges();
    copyText.style.color = "red";
  }

  const handleTextarea = (e) => {
  };

  const handleSave = () => {
    let Kumbha = localStorage.getItem("props.textareaValue6") || "";
    let Mithuna = localStorage.getItem("props.textareaValue4") || "";
    const indexKumbha  = Kumbha.indexOf("*");
    const indexMithuna = Mithuna.indexOf("*");
    console.log(" in Kumbha ",indexKumbha)
    console.log(" in Mithuna ",indexMithuna)
    let firsthalf = Kumbha.slice(0,indexKumbha).replace("*","");
    let secondhalf =  Mithuna.slice(indexMithuna).replace("*","");
    let texty = (firsthalf+secondhalf).replace("","");
    settext(texty)
  };

  return (
    <div className="container">
      <h3>Real rasi phalalu</h3>
      <div>
        <h4>2.Mithuna New</h4>
      </div>
      <textarea id = "bot6" rows = "30" cols = "auto"   value={text} onChange={handleTextarea} />
      <button className="mx-2 my-2" onClick={handleSave}>Update</button>
      <button className="mx-2 my-2" onClick={handleCopy}>Copy</button>
     
</div>
  );
};

export default MithunaNew
