import React, { useState,useEffect } from "react";
const TulaNew = () => {
  const [text, settext] = useState(" ");

  function handleCopy() {
    const copyText = document.querySelector("#bot1");
    copyText.select();
    document.execCommand("copy");
    window.getSelection().removeAllRanges();
    copyText.style.color = "red";
  }

  const handleTextarea = (e) => {
  };
  useEffect(() => {
    handleSave();
   }, []);
  const handleSave = () => {
    let dhanassu = localStorage.getItem("props.textareaValue9") || "";
    let tula = localStorage.getItem("props.textareaValue12") || "";
    const indexdhanassu  = dhanassu.indexOf("*");
    const indextula = tula.indexOf("*");
    console.log(" in dhanassu ",indexdhanassu)
    console.log(" in tula ",indextula)
    let firsthalf = dhanassu.slice(0,indexdhanassu).replace("*","");
    let secondhalf =  tula.slice(indextula).replace("*","");
    let texty = (firsthalf+secondhalf).replace("","");
    settext(texty)
  };

  return (
    <div className="container">
        <h3>Dina phalalu in telugu</h3>
      <div>
        <h4>1.Tula new</h4>
      </div>
      <textarea id = "bot1" rows = "30" cols = "auto"   value={text} onChange={handleTextarea} />
      <button className="mx-2 my-2" onClick={handleSave}>Update</button>
      <button className="mx-2 my-2" onClick={handleCopy}>Copy</button>
     
</div>
  );
};

export default TulaNew
