import React, { useState, useEffect } from "react";

const Vrushabha = (props) => {
  const [currentItem, setCurrentItem] = useState(0);
  const [isSliderVisible, setIsSliderVisible] = useState(false);
  const [luckyText, setLuckyText] = useState("");
  let [i, seti] = useState(0);
  let [pasteDone, setPasteDone] = useState(false);
  let [num, setnum] = useState(0);
  let [s,sets] = useState(0);


  // Function to generate a random number between 1 and 100
  const getRandomNumber = () => {
    return Math.floor(Math.random() * 100) + 1;
  };

  // Array of Telugu colour names
  const colours = [
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    " ",
    "",
    "",
    "",
    " ",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    "",
    " ",
    "",
    "",
    "",
    "",
    "",
  ];







     
  function handlePaste() {
    if (i < 2) {
      localStorage.setItem("a", i++);
  
      if (i === 2) {
        navigator.clipboard.readText().then((text) => {
          props.setTextareaValue3((prevValue) => prevValue + text + " * ");
          localStorage.setItem(
            "props.textareaValue10",
            props.textareaValue10 + text + " * "
          );
        });
      } else {
        navigator.clipboard.readText().then((text) => {
          props.setTextareaValue3((prevValue) => prevValue + text + " ");
          localStorage.setItem(
            "props.textareaValue10",
            props.textareaValue10 + text + " "
          );
        });
      }
    } else {
      navigator.clipboard.readText().then((text) => {
        props.setTextareaValue3((prevValue) => prevValue + text + " ");
        localStorage.setItem(
          "props.textareaValue10",
          props.textareaValue10 + text + " "
        );
      });
    }
    seti((prevI) => prevI++);
    setnum(++num)
    setPasteDone(true);
  
  setTimeout(()=>{
    setPasteDone(false);
  },3000)
  }
  
  useEffect(() => {
    let today = new Date().getDate();
    let yesterday = localStorage.getItem('date11') || "";
    if(today!=yesterday){
      handleClear();
    }
    DailyOnceSet();
    localStorage.setItem('a', 0);
    props.setTextareaValue3(localStorage.getItem("props.textareaValue10")|| "");

  }, []);
  useEffect(() => {
    let isKeyPressed = false;
    let intervalId = null;
  
    const handleKeyDown = (event) => {
      if (event.key === "3" && !isKeyPressed) {
        isKeyPressed = true;
        intervalId = setInterval(handlePaste, 100);
      }
    };
  
    const handleKeyUp = (event) => {
      if (event.key === "3") {
        isKeyPressed = false;
        clearInterval(intervalId);
      }
    };
  
    document.addEventListener("keydown", handleKeyDown);
    document.addEventListener("keyup", handleKeyUp);
  
    return () => {
      document.removeEventListener("keydown", handleKeyDown);
      document.removeEventListener("keyup", handleKeyUp);
    };
  }, []);
  
  // Function to generate a random colour name in Telugu
  const getRandomColour = () => {
    const randomIndex = Math.floor(Math.random() * colours.length);
    return colours[randomIndex];
  };

  // Update the luckyText state variable when the component mounts
  useState(() => {
    const randomNumber = getRandomNumber();
    const randomColour = getRandomColour();
    setLuckyText(` : ${randomNumber}  : ${randomColour}`);
  }, []);

  const items = [
    {
      id: 1,
      title: `3.Rasi Phalalu today Vrushabha 1 --- ${num}`,
      thumbnail: props.thumb,
      src: "https://www.astrosage.com/telugu/rasi-phalalu/vrusha-tomorrow-rasi-phalalu.asp",
    },
    {
      id: 2,
      title: `3 -         2 --- ${num}`,
      thumbnail: "",
      src: "https://www.andhrajyothy.com/astrology",
    },
    {
      id: 3,
      title: `3-         3 --- ${num}`,
      thumbnail: "",
      src: "https://www.ntnews.com/horoscope",
    },
    {
      id:4,
      title: `3-        4 --- ${num}`,
      thumbnail: "",
      src: "https://telugu.abplive.com/topic/rasi-phalalu",
    },
    
    {
      id: 5,
      title: `3-          5 ---  ${num}`,
      thumbnail: "",
      src: "https://telugu.asianetnews.com/daily-raasi-phalas",
    },

    {
      id: 6,
      title: `3-          6 --- ${num}`,
      thumbnail: "",
      src: "https://telugu.hindustantimes.com/topic/rasi-phalalu-today",
    },
    
    {
      id: 7,
      title: `3-           7-- ${num}`,
      thumbnail: "",
      src: "https://www.dishadaily.com/horoscope",
    },
    {
      id: 8,
      title: `3-           8 ---  ${num}`,
      thumbnail: "",
      src: "https://m-telugu.webdunia.com/astrology-daily-horoscope",
    },

  ];


  // Function to handle text area value and save it to local storage
  const handleTextarea = (e) => {
    props.setTextareaValue3(e.target.value);
    localStorage.setItem("props.textareaValue10", e.target.value);
  };

  const handleNext = () => {
    sets(++s);
    if(s != num)
    {
     alert("pasting error!");
     sets(--s);
     
    }
    else{
    if (currentItem === items.length - 1) {
      setCurrentItem(0);
    } else {
      setCurrentItem(currentItem + 1);
    }
  }
  };

  const handlePrev = () => {
    if (currentItem === 0) {
      setCurrentItem(items.length - 1);
    } else {
      setCurrentItem(currentItem - 1);
    }
  };

  const handleShowSlider = () => {
    setIsSliderVisible(true);
  };
 
 
  const handleSave = () => {
    props.setTextareaValue3(
      items[0].thumbnail +
        ". " +
        props.textareaValue10 +
        " " +
        luckyText
    );
    localStorage.setItem(
      "props.textareaValue10",
      items[0].thumbnail +
        ". " +
        props.textareaValue10 +
        " " +
        luckyText
    );
  };
  const DailyOnceSet = function(){
    let currentDate = new Date();
    let currentDay = currentDate.getDate();
      localStorage.setItem('date11', currentDay);
    }
    
  const handleClear = () => {
    localStorage.removeItem("props.textareaValue10");
    props.setTextareaValue3("");
  };
  const handleSave2 = () => {
    props.setTextareaValue3(props.textareaValue10.replaceAll(":-","  ").replace(" :", " ").replace(" "," ").replace(" " ," ").replace("  "," :-").replace(/\n+/g, '\n') .replace(/\s+/g, ' '));
    localStorage.setItem("props.textareaValue10", props.textareaValue10.replaceAll(":-","  ").replace(" :", " ").replace(" "," ").replace(" " ," ").replace("  "," :-").replace(/\n+/g, '\n') .replace(/\s+/g, ' ')); 
  };
  const handleShowSlider2 = () => {
    setIsSliderVisible(false);   
    handleSave();
     handleSave2();
  };

  return (
    <>
    <div className="container">
      <div>
        <h4>3.Vrushabha</h4>
      </div>
      <textarea
        id="t10"
        rows="30"
        cols="auto"
        value={props.textareaValue10}
        onChange={handleTextarea}
      />
      <button className="mx-2 my-2" onClick={handleSave}>
        Update
      </button>
      <button className="mx-2 my-2" onClick={handleSave2}>
        Save
      </button>
      <button className="mx-2 my-2" onClick={handleClear}>
        Clear
      </button>
      <button className="mx-2 my-3" onClick={() => handlePaste()}>
        Paste
      </button>
      <button className="mx-2 my-2" onClick={handleShowSlider}>
        On
      </button>
      <button className="mx-2 my-2"onClick={handleShowSlider2}>Off</button>

      <div className={`slider ${isSliderVisible ? "" : "hidden"}`}>
        <div className="card">
          <h5 className="card-title danger">{items[currentItem].title}</h5>
          <div className="card-body">
            <iframe
              className="card-img-top"
              style={{ width: "100%", height: "60rem" }}
              width="80"
              height="160"
              src={items[currentItem].src}
            ></iframe>
          </div>
        </div>
        <div className="slider-nav">
          <button className="mx-3 my-3" onClick={handlePrev}>
            Prev
          </button>
          <button className="mx-3 my-3" onClick={handleNext}>
            Next
          </button>
        </div>
      </div>
    </div>
     {pasteDone && (
      <div style={{ position: 'fixed', top: '10px', right: '10px', background: 'green', color: 'white', padding: '10px' }}>
        Paste work done! {num== 6? "Click Off" : num}
      </div>
    )}
          </>
  );
};

export default Vrushabha;
