import React, { useState,useEffect } from "react";
const DhanassuNew = () => {
  const [text, settext] = useState(" ");

  function handleCopy() {
    const copyText = document.querySelector("#bot2");
    copyText.select();
    document.execCommand("copy");
    window.getSelection().removeAllRanges();
    copyText.style.color = "red";
  }
  useEffect(() => {
   handleSave();
  }, []);
  const handleTextarea = (e) => {
  };

  const handleSave = () => {
    let mesha = localStorage.getItem("props.textareaValue7") || "";
    let Dhanassu = localStorage.getItem("props.textareaValue9") || "";
    const indexmesha  = mesha.indexOf("*");
    const indexDhanassu = Dhanassu.indexOf("*");
    console.log(" in mesha ",indexmesha)
    console.log(" in Dhanassu ",indexDhanassu)
    let firsthalf = mesha.slice(0,indexmesha).replace("*","");
    let secondhalf =  Dhanassu.slice(indexDhanassu).replace("*","");
    let texty = (firsthalf+secondhalf).replace("","");
    settext(texty)
  };

  return (
    <div className="container">
       <h3>Daily panchangam</h3>
      <div>
        <h4>1.Dhanassu New</h4>
      </div>
      <textarea id = "bot2" rows = "30" cols = "auto"   value={text} onChange={handleTextarea} />
      <button className="mx-2 my-2" onClick={handleSave}>Update</button>
      <button className="mx-2 my-2" onClick={handleCopy}>Copy</button>
     
</div>
  );
};

export default DhanassuNew
