import React, { useState ,useEffect} from "react";
const VrushabhaNew = () => {
  const [text, settext] = useState(" ");

  function handleCopy() {
    const copyText = document.querySelector("#bot5");
    copyText.select();
    document.execCommand("copy");
    window.getSelection().removeAllRanges();
    copyText.style.color = "red";
  }
  useEffect(() => {
    handleSave();
   }, []);
  const handleTextarea = (e) => {
  };

  const handleSave = () => {
    let Makara = localStorage.getItem("props.textareaValue5") || "";
    let Vrushabha = localStorage.getItem("props.textareaValue10") || "";
    const indexMakara  = Makara.indexOf("*");
    const indexVrushabha = Vrushabha.indexOf("*");
    console.log(" in Makara ",indexMakara)
    console.log(" in Vrushabha ",indexVrushabha)
    let firsthalf = Makara.slice(0,indexMakara).replace("*","");
    let secondhalf =  Vrushabha.slice(indexVrushabha).replace("*","");
    let texty = (firsthalf+secondhalf).replace("","");
    settext(texty)
  };

  return (
    <div className="container">
      <div>
        <h4>2.Vrushabha New</h4>
      </div>
      <textarea id = "bot5" rows = "30" cols = "auto"   value={text} onChange={handleTextarea} />
      <button className="mx-2 my-2" onClick={handleSave}>Update</button>
      <button className="mx-2 my-2" onClick={handleCopy}>Copy</button>
     
</div>
  );
};

export default VrushabhaNew
