


import React, { useState, useEffect } from "react";

const Mesha = (props) => {
  const [currentItem, setCurrentItem] = useState(0);
  const [isSliderVisible, setIsSliderVisible] = useState(false);
  const [luckyText, setLuckyText] = useState("");
  let [i, seti] = useState(0);
  let [pasteDone, setPasteDone] = useState(false);
  let [num, setnum] = useState(0);
  let [s,sets] = useState(0);

  // Function to generate a random number between 1 and 100
  const getRandomNumber = () => {
    return Math.floor(Math.random() * 100) + 1;
  };

  // Array of Telugu colour names
  const colours = [
    "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  " ",
  "",
  "",
  "",
  " ",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  "",
  " ",
  "",
  "",
  "",
  "",
  "",
  ];

  // Function to generate a random colour name in Telugu
  const getRandomColour = () => {
    const randomIndex = Math.floor(Math.random() * colours.length);
    return colours[randomIndex];
  };

  // Update the luckyText state variable when the component mounts
  useState(() => {
    const randomNumber = getRandomNumber();
    const randomColour = getRandomColour();
    setLuckyText(` : ${randomNumber}  : ${randomColour}`);
  }, []);

  const items = [
    {
      id: 1,
      title: `6.Rasi Phalalu today Mesha 1 --- ${num}`,
      thumbnail: props.thumb3,
      src: "https://www.astrosage.com/telugu/rasi-phalalu/mesha-tomorrow-rasi-phalalu.asp",
    },
    {
      id: 2,
      title: `6 -         2 --- ${num}`,
      thumbnail: "",
      src: "https://www.andhrajyothy.com/astrology",
    },
    {
      id: 3,
      title: `6-         3 --- ${num}`,
      thumbnail: "",
      src: "https://www.ntnews.com/horoscope",
    },
    {
      id:4,
      title: `6-        4 --- ${num}`,
      thumbnail: "",
      src: "https://telugu.abplive.com/topic/rasi-phalalu",
    },
    
    {
      id: 5,
      title: `6-          5 ---  ${num}`,
      thumbnail: "",
      src: "https://telugu.asianetnews.com/daily-raasi-phalas",
    },

    {
      id: 6,
      title: `6-          6 --- ${num}`,
      thumbnail: "",
      src: "https://telugu.hindustantimes.com/topic/rasi-phalalu-today",
    },
    
    {
      id: 7,
      title: `6-           7-- ${num}`,
      thumbnail: "",
      src: "https://www.dishadaily.com/horoscope",
    },
    {
      id: 8,
      title: `6-           8 ---  ${num}`,
      thumbnail: "",
      src: "https://m-telugu.webdunia.com/astrology-daily-horoscope",
    },

  ];

  const DailyOnceSet = function(){
    let currentDate = new Date();
    let currentDay = currentDate.getDate();
      localStorage.setItem('date5', currentDay);
    }
    


  // Function to handle text area value and save it to local storage
  const handleTextarea = (e) => {
    props.setTextareaValue1(e.target.value);
    localStorage.setItem("props.textareaValue7", e.target.value);

  };

  const handleNext = () => {
    sets(++s);
    if(s != num)
    {
      alert("pasting error!");
     sets(--s);
    }
    else{
    if (currentItem === items.length - 1) {
      setCurrentItem(0);
    } else {
      setCurrentItem(currentItem + 1);
    }
  }
  };

  const handlePrev = () => {
    if (currentItem === 0) {
      setCurrentItem(items.length - 1);
    } else {
      setCurrentItem(currentItem - 1);
    }
  };
 
  


     
  function handlePaste() {
    if (i < 2) {
      localStorage.setItem("e", i++);
  
      if (i === 2) {
        navigator.clipboard.readText().then((text) => {
          props.setTextareaValue1((prevValue) => prevValue + text + " * ");
          localStorage.setItem(
            "props.textareaValue7",
            props.textareaValue7 + text + " * "
          );
        });
      } else {
        navigator.clipboard.readText().then((text) => {
          props.setTextareaValue1((prevValue) => prevValue + text + " ");
          localStorage.setItem(
            "props.textareaValue7",
            props.textareaValue7 + text + " "
          );
        });
      }
    } else {
      navigator.clipboard.readText().then((text) => {
        props.setTextareaValue1((prevValue) => prevValue + text + " ");
        localStorage.setItem(
          "props.textareaValue7",
          props.textareaValue7 + text + " "
        );
      });
    }
    seti((prevI) => prevI++);
    setnum(++num)
    setPasteDone(true);
  
  setTimeout(()=>{
    setPasteDone(false);
  },3000)
  }
  
  useEffect(() => {
    let today = new Date().getDate();
    let yesterday = localStorage.getItem('date5') || "";
    if(today!=yesterday){
      handleClear();
    }
    DailyOnceSet();
    localStorage.setItem('e', 0);
   props.setTextareaValue1(localStorage.getItem("props.textareaValue7") || "");

  }, []);
  useEffect(() => {
    let isKeyPressed = false;
    let intervalId = null;
  
    const handleKeyDown = (event) => {
      if (event.key === "6" && !isKeyPressed) {
        isKeyPressed = true;
        intervalId = setInterval(handlePaste, 100);
      }
    };
  
    const handleKeyUp = (event) => {
      if (event.key === "6") {
        isKeyPressed = false;
        clearInterval(intervalId);
      }
    };
  
    document.addEventListener("keydown", handleKeyDown);
    document.addEventListener("keyup", handleKeyUp);
  
    return () => {
      document.removeEventListener("keydown", handleKeyDown);
      document.removeEventListener("keyup", handleKeyUp);
    };
  }, []);
  





  const handleShowSlider = () => {
    setIsSliderVisible(true);
  };

  const handleSave = () => {
    props.setTextareaValue1(items[0].thumbnail+ ". "+(props.textareaValue7)+ " "+luckyText);
    localStorage.setItem("props.textareaValue7",items[0].thumbnail+ ". "+props.textareaValue7+ " "+luckyText); 
  };
  const handleSave2 = () => {
    props.setTextareaValue1(props.textareaValue7.replaceAll(":-","  ").replace(" :", " ").replace(" "," ").replace(" " ," ").replace("  "," :-").replace(/\n+/g, '\n') .replace(/\s+/g, ' '));
    localStorage.setItem("props.textareaValue7", props.textareaValue7.replaceAll(":-","  ").replace(" :", " ").replace(" "," ").replace(" " ," ").replace("  "," :-").replace(/\n+/g, '\n') .replace(/\s+/g, ' ')); 
  };
  
  const handleClear = () => {
    localStorage.removeItem("props.textareaValue7");
    props.setTextareaValue1("");
  };
  const handleShowSlider2 = () => {
    setIsSliderVisible(false);   
    handleSave();
     handleSave2();
  };

  return (
    <>
    <div className="container">
      <div>
        <h4>6.Mesha</h4>
      </div>
      <textarea id = "t7" rows = "30" cols = "auto" value={props.textareaValue7} onChange={handleTextarea} />
      <button className="mx-2 my-2" onClick={handleSave}>Update</button>
      <button className="mx-2 my-2" onClick={handleSave2}>Save</button>
      <button className="mx-2 my-2" onClick={handleClear}>Clear</button>
      <button className="mx-2 my-3" onClick={() =>handlePaste()}>
            Paste
          </button>
      <button className="mx-2 my-2"onClick={handleShowSlider}>On</button>
      <button className="mx-2 my-2"onClick={handleShowSlider2}>Off</button>

      <div className={`slider ${isSliderVisible ? "" : "hidden"}`}>
        <div className="card">
          <h5 className="card-title danger">{items[currentItem].title}</h5>
          <div className="card-body">
            <iframe
              className="card-img-top"
              style={{ width: "100%", height: "60rem" }}
              width="80"
              height="160"
              src={items[currentItem].src}
            ></iframe>
          </div>
        </div>
        <div className="slider-nav">
          <button className="mx-3 my-3"onClick={handlePrev}>Prev</button>
          <button className="mx-3 my-3"onClick={handleNext}>Next</button>
        </div>
      </div>
    </div>
     {pasteDone && (
      <div style={{ position: 'fixed', top: '10px', right: '10px', background: 'green', color: 'white', padding: '10px' }}>
        Paste work done! {num== 6? "Click Off": num}
      </div>
    )}
          </>
  );
};

export default Mesha
