import React, { useState,useEffect } from "react";
const MakaraNew = () => {
  const [text, settext] = useState(" ");

  function handleCopy() {
    const copyText = document.querySelector("#bot8");
    copyText.select();
    document.execCommand("copy");
    window.getSelection().removeAllRanges();
    copyText.style.color = "red";
  }

  const handleTextarea = (e) => {
  };
  useEffect(() => {
    handleSave();
   }, []);
  const handleSave = () => {
    let vrushabh = localStorage.getItem("props.textareaValue10") || "";
    let Makara = localStorage.getItem("props.textareaValue5") || "";
    const indexvrushabh  = vrushabh.indexOf("*");
    const indexMakara = Makara.indexOf("*");
    console.log(" in vrushabh ",indexvrushabh)
    console.log(" in Makara ",indexMakara)
    let firsthalf = vrushabh.slice(0,indexvrushabh).replace("*","");
    let secondhalf =  Makara.slice(indexMakara).replace("*","");
    let texty = (firsthalf+secondhalf).replace("","");
    settext(texty)
  };

  return (
    <div className="container">
      <div>
        <h4>2.Makara New</h4>
      </div>
      <textarea id = "bot8" rows = "30" cols = "auto"   value={text} onChange={handleTextarea} />
      <button className="mx-2 my-2" onClick={handleSave}>Update</button>
      <button className="mx-2 my-2" onClick={handleCopy}>Copy</button>
     
</div>
  );
};

export default MakaraNew
