import React, { useState,useEffect } from "react";
const KumbhaNew = () => {
  const [text, settext] = useState(" ");

  function handleCopy() {
    const copyText = document.querySelector("#bot3");
    copyText.select();
    document.execCommand("copy");
    window.getSelection().removeAllRanges();
    copyText.style.color = "red";
  }
  useEffect(() => {
    handleSave();
   }, []);
  const handleTextarea = (e) => {
  };

  const handleSave = () => {
    let Vruschika = localStorage.getItem("props.textareaValue11") || "";
    let Kumbha = localStorage.getItem("props.textareaValue6") || "";
    const indexVruschika  = Vruschika.indexOf("*");
    const indexKumbha = Kumbha.indexOf("*");
    console.log(" in Vruschika ",indexVruschika)
    console.log(" in Kumbha ",indexKumbha)
    let firsthalf = Vruschika.slice(0,indexVruschika).replace("*","");
    let secondhalf =  Kumbha.slice(indexKumbha).replace("*","");
    let texty = (firsthalf+secondhalf).replace("","");
    settext(texty)
  };

  return (
    <div className="container">
       <h3>Daily rasi phalithalu</h3>
      <div>
        <h4>1.Kumbha New</h4>
      </div>
      <textarea id = "bot3" rows = "30" cols = "auto"   value={text} onChange={handleTextarea} />
      <button className="mx-2 my-2" onClick={handleSave}>Update</button>
      <button className="mx-2 my-2" onClick={handleCopy}>Copy</button>
     
</div>
  );
};

export default KumbhaNew
