import React, { useState ,useEffect} from "react";
const VruschikaNew = () => {
  const [text, settext] = useState(" ");

  function handleCopy() {
    const copyText = document.querySelector("#bot9");
    copyText.select();
    document.execCommand("copy");
    window.getSelection().removeAllRanges();
    copyText.style.color = "red";
  }

  const handleTextarea = (e) => {
  };
  useEffect(() => {
    handleSave();
   }, []);
  const handleSave = () => {
    let Makara = localStorage.getItem("props.textareaValue5") || "";
    let Vruschika = localStorage.getItem("props.textareaValue11") || "";
    const indexMakara  = Makara.indexOf("*");
    const indexVruschika = Vruschika.indexOf("*");
    console.log(" in Makara ",indexMakara)
    console.log(" in Vruschika ",indexVruschika)
    let firsthalf = Makara.slice(0,indexMakara).replace("*","");
    let secondhalf =  Vruschika.slice(indexVruschika).replace("*","");
    let texty = (firsthalf+secondhalf).replace("","");
    settext(texty)
  };

  return (
    <div className="container">
      <h3>New rasi phalalu</h3>
      <div>
        <h4>1.Vruschika New</h4>
      </div>
      <textarea id = "bot9" rows = "30" cols = "auto"   value={text} onChange={handleTextarea} />
      <button className="mx-2 my-2" onClick={handleSave}>Update</button>
      <button className="mx-2 my-2" onClick={handleCopy}>Copy</button>
     
</div>
  );
};

export default VruschikaNew
